import React, { Component } from 'react';
import {
    Card, CardImg, CardText, CardBody, Breadcrumb, BreadcrumbItem,
    CardTitle
} from 'reactstrap';
import { Link } from 'react-router-dom'
import { Form, FormGroup, Input, Label, FormFeedback, Modal, Button, ModalHeader, ModalBody } from 'reactstrap';
import { Loading } from './LoadingComponent';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';
import { baseUrl } from '../shared/baseUrl';
class CommentForm extends Component {
    constructor(props) {
        // console.log("CommentForm is created!!")
        super(props);
        this.state = {
            isNavOpen: false,
            isModalOpen: false,
            touched: {
                yourname: false
            }
        }
        this.toggleNav = this.toggleNav.bind(this);   // ****
        this.toggleModal = this.toggleModal.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    validate(yourname) {
        const errors = {
            yourname: ''
        };
        if (yourname != null) {
            // console.log(yourname.length);
        }
        if (this.state.touched.yourname && yourname.length < 3)
            errors.yourname = 'Must be greater than 2 characters';
        else if (this.state.touched.yourname && yourname.length > 15)
            errors.yourname = 'Must be 15 characters or less';

        return errors;
    }
    handleBlur = (field) => (evt) => {
        this.setState({
            touched: { ...this.state.touched, [field]: true },
        });
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }
    handleSubmit(event) {
        this.toggleModal();
        // alert("author: " + this.yourname.value + " rating: " + this.rating.value
        //     + " comment: " + this.comment.value);
        event.preventDefault();
        console.log("author: " + this.yourname.value + " rating: " + this.rating.value
            + " comment: " + this.comment.value);
        this.props.postComment(this.props.dishId, this.rating.value, this.yourname.value, this.comment.value);
    }
    toggleNav() {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }
    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }
    render() {


        const errors = this.validate(this.state.yourname);
        return (
            <React.Fragment>
                <Button outline onClick={this.toggleModal}><span className="fa fa-pencil fa-lg"></span> Submit Comment</Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.handleSubmit}>
                            <FormGroup>
                                <Label htmlFor="rating">Rating</Label>
                                <Input type="select" id="rating" name="rating"
                                    innerRef={(input) => this.rating = input}>
                                    <option>1</option>
                                    <option>2</option>
                                    <option>3</option>
                                    <option>4</option>
                                    <option>5</option>
                                </Input>
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="yourname">Your Name</Label>
                                <Input type="text" id="yourname" name="yourname"
                                    valid={errors.yourname === ''}
                                    invalid={errors.yourname !== ''}
                                    onBlur={this.handleBlur('yourname')}
                                    onChange={this.handleInputChange}
                                    placeholder="Your Name"
                                    innerRef={(input) => this.yourname = input} />
                                <FormFeedback>{errors.yourname}</FormFeedback>
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="comment">Comment</Label>
                                <Input type="textarea" rows="8" id="comment" name="comment"
                                    innerRef={(input) => this.comment = input} />


                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Submit</Button>
                        </Form>
                    </ModalBody>
                </Modal>
            </React.Fragment >
        );
    }


}

function RenderComments({ comments, postComment, dishId }) {
    // console.log(comments)
    if (comments != null) {
        return (
            <div>
                <h4>Comments</h4>
                <ul className="list-unstyled">
                    <Stagger in>
                        {comments.map((comment) => {
                            return (
                                <Fade in>
                                    <li key={comment.id}>
                                        <p>{comment.comment}</p>
                                        <p>-- {comment.author} , {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit' }).format(new Date(Date.parse(comment.date)))}</p>
                                    </li>
                                </Fade>
                            );
                        })}
                    </Stagger>
                </ul>
                <CommentForm dishId={dishId} postComment={postComment} />
            </div>

        );
    }
    return (<div></div>);
}
function RenderDetail({ dish }) {

    // console.log(dish)
    if (dish != null) {
        return (
            <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg top src={baseUrl + dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>

        )
    }
    return (<div></div>);
}

const DishDetail = (props) => {
    if (props.isLoading) {
        return (
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if (props.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
    else if (props.dish != null) {
        // console.log(props.comments)
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to="/menu">Menu </Link></BreadcrumbItem>
                        <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>{props.dish.name}</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDetail dish={props.dish} />
                    </div>
                    <RenderComments comments={props.comments}
                        postComment={props.postComment}
                        dishId={props.dish.id}
                    />
                </div>
            </div>
        );
    }
    return (<div></div>);
}

export default DishDetail;

